
<script>
import { mapGetters, mapActions } from 'vuex'
import _game from "@/utils/api/game"

export default {
  name: 'SportHistory',
  data () {
    return {
      // 選擇日期
      selectItems: {
        selected: 0,
        options: []
      },
      // 拆帳
      selectChangeItems: {
        selected: 1,
        options: [
          { text: '100％', value: 1 },
          { text: '自己', value: 0 }
        ]
      },
      // 每頁顯示筆數
      selectRowsPerPageItems: {
        selected: 15,
        options: [
          { text: '15筆', value: 15 },
          { text: '30筆', value: 30 },
          { text: '60筆', value: 60 }
        ]
      },
      // 玩法
      selectGameItems: {
        selected: 'N',
        options: [
          { text: '一般玩法', value: 'N' },
          { text: '多種玩法', value: 'Y' }
        ]
      },
      // 列表資料
      sportsList: {},
      pagination: {
        currentPage: 1, // 當前頁數
        rowsPerPage: 15, // 每頁筆數
        totalItems: 0 // 總筆數
      },
      // 頁面載入狀態
      loadingStatus: false,
      // 歷史紀錄傳入參數
      gamelist: {
        act: 'normal',
        ball: '',
        time_interval: 'history',
        start: 1,
        occupy: 1,
        count_date: '',
        multi: '',
        page: 1,
        count: ''
      }
    }
  },
  computed: {
    ...mapGetters({
      getSportsState: 'sports/getSportsState'
      // 載入個人帳號
      // getProfileState: 'account-management/getProfileState'
    }),
    pages () {
      if (this.pagination.rowsPerPage == null || this.pagination.totalItems == null) return 0
      return Math.ceil(this.pagination.totalItems / this.pagination.rowsPerPage)
    }
  },
  watch: {
    '$store.state.lang' (value) {
      // 刷新日期選單語系
      this.createSelectDateMenu(this.selectItems.selected)
    },
    getSportsState (payload) {
      // 調整獨立聯盟名稱位置而重組資料
      let list = {};
      // console.log('歷史比賽 data_list: ', payload.result.data_list);
      (payload.result.data_list).forEach(el => {
        let l_name = `${el.league_name}${el.transType}`
        if(!list[l_name]) list[l_name] = [] ;
        list[l_name].push(el)
      });
      this.sportsList = list
      // console.log('歷史比賽 sportsList: ', list);

      // 若 API 有當前頁數回傳資料，可設定如下
      this.pagination.currentPage = payload.result.current_page
      this.pagination.totalItems = payload.result.total_count
    },
    'pagination.rowsPerPage' (rowsPerPage) {
      // 回第一頁
      this.pagination.currentPage = 1
      // console.log(1)
      // this.loadSports()
    },
    // (每頁顯示)切換
    'selectRowsPerPageItems.selected' (value) {
      // 回第一頁
      this.gamelist.page = 1
      this.pagination.currentPage = 1

      this.pagination.rowsPerPage = value
      this.loadSports()
    },
    // (日期)切換
    'selectItems.selected' (value) {
      // 回第一頁
      this.gamelist.page = 1
      this.pagination.currentPage = 1

      this.pagination.rowsPerPage = value
      this.loadSports()
    },
    'selectTimeItems.selected' (value) {
      this.pagination.rowsPerPage = value
      this.loadSports()
    },
    // (拆帳)切換
    'selectChangeItems.selected' (value) {
      // 回第一頁
      this.gamelist.page = 1
      this.pagination.currentPage = 1

      this.gamelist.occupy = value
      this.loadSports()
    },
    // (玩法)切換
    'selectGameItems.selected' (value) {
      // 回第一頁
      this.gamelist.page = 1
      this.pagination.currentPage = 1

      // 顯示切換玩法資料表
      if (value === 'N') {
        if (this.tabledata1 !== undefined) this.tabledata1.show = true
        if (this.tabledata2 !== undefined) this.tabledata2.show = false
        // 依照選擇玩法搜尋表格
        this.loadSports()
      } else if (value === 'Y') {
        if (this.tabledata1 !== undefined) this.tabledata2.show = true
        if (this.tabledata2 !== undefined) this.tabledata1.show = false
        this.loadSports()
      }
    },
    getProfileState (payload) {
      this.gamelist.account = payload.result.l_no
    }
  },
  mounted () {
    this.createSelectDateMenu()

    // this.loadSports()
    // console.log(8)
    // 顯示全部玩法資料表
    if (this.selectGameItems.selected === 'N') {
      if (this.tabledata1 !== undefined) this.tabledata1.show = true
      if (this.tabledata2 !== undefined) this.tabledata2.show = false
      // 依照選擇玩法搜尋表格
      // console.log(9)
      // this.loadSports()
    }
  },
  methods: {
    ...mapActions({
      getSports: 'api/getSports'
    }),
    // 載入歷史比賽
    async loadSports (page, rowsPerPage) {
      // 載入狀態(開始)
      this.loadingStatus = true
      let params = {
        act: this.gamelist.act,
        ball: this.gamelist.ball,
        time_interval: this.gamelist.time_interval,
        start: this.gamelist.start,
        multi: this.selectGameItems.selected,
        occupy: this.gamelist.occupy ? this.gamelist.occupy : this.selectChangeItems.selected,
        // 正確日期要打開這行
        count_date: this.selectItems.selected,
        // 塞入假日期
        // count_date: this.gamelist.count_date,
        page: page !== undefined ? page : this.pagination.currentPage,
        count: this.pagination.rowsPerPage = this.gamelist.count ? this.gamelist.count : this.selectRowsPerPageItems.selected
      }
      console.log('history111')
      await _game.getSports(this.$store,params)
      console.log('history')
      this.loadingStatus = false
      // this.getSports(params)
      //   .then(res => {
      //     if (res.status === 200) {
      //       // 載入狀態(結束)
      //       this.loadingStatus = false
      //     }
      //   })
      //   .catch(err => {
      //     console.log(err)
      //   })
    },
    // 載入下一頁
    loadNextPage (page) {
      // 若 API 有當前頁數回傳資料，可註解以下
      this.pagination.currentPage = page
      this.loadSports()
    },
    /**
     * 建立日期選單
     * @params  [currentSelected]  當前選擇日期
     **/
    createSelectDateMenu (currentSelected) {
      let days = [
        this.$ln('星期日'),
        this.$ln('星期一'),
        this.$ln('星期二'),
        this.$ln('星期三'),
        this.$ln('星期四'),
        this.$ln('星期五'),
        this.$ln('星期六')
      ]
      let options = []
      let newDate = new Date()
      newDate.setDate(newDate.getDate() + 1)
      for (let i = 0; i < 8; i++) {
        options.push({
          text: this.$ln(`%s月%s日 %s`, (newDate.getMonth() + 1), newDate.getDate(), days[newDate.getDay()]),
          value: `${newDate.getFullYear()}-${(newDate.getMonth() + 1 < 10 ? '0' : '')}${(newDate.getMonth() + 1)}-${(newDate.getDate() < 10 ? '0' : '')}${newDate.getDate()}`
        })
        newDate.setDate(newDate.getDate() - 1)
      }
      this.selectItems.selected = currentSelected ? currentSelected : options[1].value
      this.selectItems.options = options
    }
  }
}
</script>
