<template>
  <div class="historyBasket">
    <v-container fluid>
      <h3 class="my-0 pb-3">{{ $ln(gname) }} - {{ $ln('歷史比賽') }}</h3>

      <v-layout
        row
        wrap
        class="my-0">
        <SportBallStatus
          :dataSelect.sync="selectItems.selected"
          :items="selectItems.options"
          :setLanguage="false"
          title="日期"
          class="selectHide"
        />

        <SportBallStatus
          :dataSelect.sync="selectChangeItems.selected"
          :items="selectChangeItems.options"
          title="拆帳"
        />

        <SportBallStatus
          :dataSelect.sync="selectGameItems.selected"
          :items="selectGameItems.options"
          title="玩法"
        />

        <v-flex
          md3
          xs6
          class="px-1">
          <v-layout
            row
            wrap>
            <SportBallStatus
              classType="classType"
              :dataSelect.sync="selectRowsPerPageItems.selected"
              :items="selectRowsPerPageItems.options"
              title="每頁顯示"
            />
          </v-layout>
        </v-flex>
        <v-flex
          xs4
          md2
          class="grey--text text--darken-2 d-flex align-center">
          {{ $ln('共 %s 筆', pagination.totalItems) }}
        </v-flex>
      </v-layout>

      <div
        v-if="loadingStatus"
        class="text-xs-center">
        <MaterialLoadingPage/>
      </div>
      <div v-else class="pt-1">
        <!-- 一般玩法 -->
        <SportHistoryTable
          v-if="tabledata1.show"
          :header.sync="tabledata1.headers"
          :contents.sync="sportsList"
          :path="path"
          gameType="cpb"
        />

        <!-- 多種玩法 -->
        <SportHistoryTableMuti
          v-if="tabledata2.show"
          :header.sync="tabledata2.headers2"
          :contents.sync="sportsList"
          :path="path"
        />

        <div class="text-xs-center pt-2">
          <v-pagination
            v-model="pagination.currentPage"
            :length="pages"
            color="deep-orange darken-1"
            @input="loadNextPage"/>
        </div>
      </div>
    </v-container>
  </div>
</template>

<script>
import History from '@/mixins/sport/History'

export default {
  name: '',
  mixins:[History],
  data () {
    return {
      gname: '台棒',
      // 連結注單明細
      path: '/manage/sport/cpb/CheckView',
      // 一般玩法
      tabledata1: {
        show: false,
        // showNoData: '目前無資料',
        headers: {
         //'league_name' : { text: '聯盟名稱', align: 'center', sortable: false }, // 已被獨立成一行
         'count_date' :  { text: '帳務日期', align: 'center', sortable: false },
         'game_time' :  { text: '比賽時間', align: 'center', sortable: false },
         'team' :  { text: '主客隊', align: 'center', sortable: false },
         'A' :  { text: '讓分', align: 'center', sortable: false },
         'BS' :  { text: '大小', align: 'center', sortable: false },
         'OL' :  { text: '獨贏', align: 'center', sortable: false },
         'OT' :  { text: '一輸二贏', align: 'center', sortable: false },
         'SD' :  { text: '單雙', align: 'center', sortable: false },
         'detail' :  { text: '明細', align: 'center', sortable: false }
        }
      },
      // 多種玩法
      tabledata2: {
        show: false,
        // showNoData: '目前無資料',
        headers2: {
          //'league_name' : { text: '聯盟名稱', align: 'left', sortable: false, class:'text-xs-left red--text' }, // 已被獨立成一行
          'count_date' : { text: '帳務日期', align: 'center', sortable: false },
          'game_time' : { text: '比賽時間', align: 'center', sortable: false },
          'team' : { text: '主客隊', align: 'center', sortable: false },
          'point_1' : { text: '搶首分', align: 'center', sortable: false },
          'point_2' : { text: '搶尾分', align: 'center', sortable: false },
          'point_high' : { text: '單節最高分', align: 'center', sortable: false },
          'detail' : { text: '明細', align: 'center', sortable: false }
        }
      },
      // 歷史紀錄傳入參數
      gamelist: {
        act: 'normal',
        ball: 3,
        time_interval: 'history',
        start: 1,
        occupy: '',
        count_date: '',
        multi: '',
        page: '',
        count: ''
      }
    }
  },
  computed: {
  },
  watch: {
  },
  mounted () {
  },
  methods: {
  }
}
</script>
